import Modal from 'react-modal'
import { Container, TransactionTypeConatiner, RadioBox } from './style';
import incomeImg from './../../assets/income.svg'
import outcomeImg from './../../assets/outcome.svg'
import closeImg from './../../assets/close.svg'
import { FormEvent, useState } from 'react';
import { v4 } from 'uuid'
import { api } from '../../api/api';
import { useContext } from 'react';
import { TransactionsContext } from '../../TransactionsContext';


interface NewTransactionModalProps {
    isOpen: boolean;
    onRequestClose: () => void;
}


export function NewTransactionModal({ isOpen, onRequestClose }:NewTransactionModalProps){

    const [type, setType] = useState('deposit')
    const [amount, setAmount] = useState(0)
    const [title, setTitle] = useState('')
    const [category, setCategory] = useState('')

    const {transactionsList, setTransactionsList} = useContext(TransactionsContext) 

    async function handleCreateNewTransaction(event: FormEvent){
        event.preventDefault()

        const data = {
            id: v4(),
            amount, 
            title, 
            category,
            type, 
            created_at: new Date().toLocaleDateString()
        }

        const res = await api.post('/transactions',data)
        

        setAmount(0)
        setCategory('')
        setTitle('')
        setType('deposit')
        
        setTransactionsList([...transactionsList, res.data?.transaction])
        
    }

    return (
        <Modal 
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            overlayClassName="react-modal-overlay"
            className="react-modal-content"
        >
            <button  className='react-modal-close' type="button" onClick={onRequestClose}>
                <img src={closeImg} alt="Fechar modal" />
            </button>

            <Container
                onSubmit={handleCreateNewTransaction}
            >
                <h2>Cadastrar transao</h2>
                
                <input 
                    placeholder='Ttulo'
                    value={title}
                    onChange={(event) => setTitle(event.currentTarget.value)}
                />
                <input 
                    type="number" 
                    placeholder='Valor' 
                    value={amount}
                    onChange={(event) => setAmount(Number(event.currentTarget.value))}
                    
                />
                
                <TransactionTypeConatiner>
                    
                    <RadioBox 
                        type='button'
                        onClick={() => {setType('deposit');}}
                        isActve={type === 'deposit'}
                        activeColor="green"
                    >
                        <img src={incomeImg} alt="" />
                        <span>Entrada</span>
                    </RadioBox>
                    
                    <RadioBox 
                        type="button"
                        onClick={() => { setType('withdraw');} }
                        isActve={type === 'withdraw'}
                        activeColor="red"

                    >
                        <img src={outcomeImg} alt="" />
                        <span>Sada</span>
                    </RadioBox>

                </TransactionTypeConatiner>
                
                <input 
                    placeholder='Categoria' 
                    value={category}
                    onChange={(event) => setCategory(event.currentTarget.value)}
                />
                <button type="submit">Cadastrar</button>
                
            </Container>
        </Modal>
    )
}